@charset "UTF-8";
body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

input,
textarea,
button {
  border-radius: 0;
}

/* stylelint-disable */
/* stylelint-enable */
:root {
  box-sizing: border-box;
  font-size: 0.875em;
}

*,
::before,
::after {
  box-sizing: inherit;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  line-height: 1;
}

body::-webkit-scrollbar {
  width: 16px;
  /* ширина всей полосы прокрутки */
}

body::-webkit-scrollbar-track {
  background: #f1f1f1;
  /* цвет зоны отслеживания */
}

body::-webkit-scrollbar-thumb {
  background-color: #c1c1c1;
  /* цвет бегунка */
  border-radius: 4px;
  /* округлось бегунка */
  border: 1px solid #f1f1f1;
  /* отступ вокруг бегунка */
}

body::-webkit-scrollbar-thumb:hover {
  background-color: rgba(193, 193, 193, 0.9);
}

body::-webkit-scrollbar-thumb:active {
  background-color: rgba(193, 193, 193, 0.8);
}

